<template>
  <div class="contact">
    <h1>联系我</h1>
    <transition enter-active-class="animated bounceInDown">
      <div class="main" v-show="show">
        <ul>
          <li>
            <h2 class="title">自我评价</h2>
            <div class="des">
              <p>高效的自学能力,具备独立分析解决问题能力</p>
              <p>强烈的自我驱动力,只喜欢优雅的代码</p>
              <p>熟悉组件化开发</p>
            </div>
          </li>
          <li>
            <h2 class="title">兴趣爱好</h2>
            <div class="des">
              <p>热爱前端,F1车迷,偶尔玩玩LOL</p>
            </div>
          </li>
          <li>
            <h2 class="title">联系方式</h2>
            <div class="des">
              <p>
                <span class="box animated tada" style="animation-delay: 1s"><i class="icon-shouji"></i></span>
                <span>17327811602</span>
              </p>
              <p>
                <span class="box animated tada" style="animation-delay: 1.4s"><i class="icon-email"></i></span>
                <span>1586628500@qq.com</span>
              </p>
              <p>
                <span class="box animated tada" style="animation-delay: 1.8s"><i class="icon-qq"></i></span>
                <span>1586628500</span>
              </p>
              <p class="weixin">
                <span class="box animated tada" style="animation-delay: 2.2s"><i class="icon-weixin"></i></span>
                <img src="../common/img/qrcode.jpg" width="150" height="150" alt="微信二维码">
              </p>
            </div>
          </li>
        </ul>
      </div>
    </transition>
  </div>
</template>

<script>
  import 'animate.css'

  export default {
    name: "contact",
    data() {
      return {
        show: false
      }
    },
    mounted() {
      clearTimeout(this.timer)
      this.timer = setInterval(() => {
        this.show = true
      }, 500)
    }
  }
</script>

<style lang="scss">
  .contact {
    padding: 10px 0;
    text-align: center;
    > h1 {
      padding: 10px 0;
      font-size: 18px;
      font-weight: bold;
    }
    .main {
      margin: 10px 5% 0;
      padding-top: 10px;
      background: rgba(0, 0, 0, 0.4);
      border-radius: 10px;
      transition: all 0.3s ease;
      @media screen and (min-width: 768px) {
        margin: 10px 20% 0;
      }
      @media screen and (min-width: 992px) {
        margin: 10px 24% 0;
      }
      ul {
        li {
          padding: 10px;
          &:last-child {
            .box {
              display: inline-block;
            }
          }
          .title {
            font-size: 16px;
            font-weight: 700;
          }
          p {
            &.weixin {
              margin-top: 10px;
              i {
                font-size: 24px;
              }
            }
          }
        }
      }
    }
  }
</style>



