<template>
  <div class="skills">
    <h1>主要技能</h1>
    <transition enter-active-class="animated slideInRight faster">
      <div class="main" v-show="show">
        <ul>
          <li v-for="item in skills">
            <h3 class="title">{{item.text}}</h3>
            <div class="progress-bar">
              <div class="rect"></div>
              <span class="dot" ref="dots"></span>
            </div>
          </li>
        </ul>
      </div>
    </transition>
  </div>
</template>

<script>

  export default {
    name: "skills",
    data() {
      return {
        skills: [
          {
            text: 'html/css/scss/ps',
            percent: 0.75
          },
          {
            text: 'javascript/jquery',
            percent: 0.7
          },
          {
            text: 'vue/vue-router/vuex/vue-ssr',
            percent: 0.75
          },
          {
            text: 'webpack/parcel-bundler',
            percent: 0.6
          },
          {
            text: 'node.js/express/koa',
            percent: 0.5
          }
        ],
        show: false
      }
    },
    mounted() {

      for (let i = 0; i < this.skills.length; i++) {
        let item = this.$refs.dots[i]
        setTimeout(() => {
          let $width = this.$refs.dots[0].previousElementSibling.clientWidth
          item.style.transform = `translate3d(${this.skills[i].percent * $width}px,0,0)`
        }, 1200)
      }

      clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        this.show = true
      }, 500)
    }
  }
</script>

<style lang="scss">
  @import "../common/scss/var";

  .skills {
    padding: 10px 0;
    text-align: center;
    > h1 {
      padding: 10px 0;
      font-size: 18px;
      font-weight: bold;
    }
    .main {
      margin: 10px 5% 0;
      /*background: rgba(0, 0, 0, 0.4);*/
      @media screen and (min-width: 769px) {
        margin: 10px 20% 0;
      }
      ul {
        width: 100%;
        li {
          padding: 10px 0;
          width: 100%;
          text-align: left;
          .title {
            position: relative;
            line-height: 24px;
            font-size: 18px;
            background: $color-theme;
            display: inline-block;
            padding-left: 10px;
            box-shadow: 0 4px 4px rgba(0, 0, 0, 0.4);
            &::after {
              content: '';
              position: absolute;
              top: 0;
              border-left: 12px solid $color-theme;
              border-top: 12px solid transparent;
              border-bottom: 12px solid transparent;
            }
          }
          .progress-bar {
            position: relative;
            margin-top: 15px;
            height: 10px;
            .rect {
              width: 100%;
              height: 10px;
              background: #f0f0f0;
            }
            .dot {
              width: 20px;
              height: 20px;
              position: absolute;
              top: -5px;
              left: 0;
              background: #fff;
              border: 3px solid $color-theme;
              border-radius: 50%;
              transition: all 1s ease;
            }
          }
        }
      }
    }
  }
</style>



