<template>
  <div class="projects">
    <h1>个人项目/作品</h1>
    <transition enter-active-class="animated lightSpeedIn fast">
      <div class="main" v-show="show">
        <ul>
          <li>
            <h3 class="title">cnode社区移动端webapp</h3>
            <div class="description">
              <p>该项目是基于Cnodejs的api,采用vue.js重写的移动端单页应用.</p>
              <p>技术栈: vue,vue-cli,vue-router,vuex,jquery</p>
              <p>
                功能实现:登录功能,首页话题列表展示和类别切换,上拉加载更多,图片的懒加载,话题详情页的数据渲染,新建主题,编辑主题,收藏主题,取消收藏主题,查看用户所收藏的主题,新建评论,为评论点赞,用户详情,消息通知,标记消息已读,404页面,回到顶部
              </p>
            </div>
            <div class="site">
              <p>github地址: <a href="https://github.com/lewis878/vue-cnode">https://github.com/lewis878/vue-cnode</a></p>
              <p>预览地址: <a href="https://cjycc.github.io/cnode">https://cjycc.github.io/cnode</a></p>
            </div>
          </li>
          <li>
            <h3 class="title">mobile-slide</h3>
            <div class="description">
              <p>用vue.js写的移动端无缝轮播图插件</p>
              <p>技术栈: vue,javascript</p>
              <p>
                功能实现:手指滑动切换,自动轮播,自定义切换时间间隔以及轮播速度
              </p>
            </div>
            <div class="site">
              <p>github地址: <a
                href="https://github.com/lewis878/mobile-slide">https://github.com/lewis878/mobile-slide</a>
              </p>
              <p>预览地址: <a href="https://cjycc.github.io/mobile-slide">https://cjycc.github.io/mobile-slide</a></p>
            </div>
          </li>
          <li>
            <h3 class="title">contacts-like</h3>
            <div class="description">
              <p>在vue.js中用原生js写的类通讯录组件</p>
              <p>技术栈: vue.js, Velocity.js, jsonp</p>
              <p>
                功能实现:根据shortcut快速查找联系人
              </p>
            </div>
            <div class="site">
              <p>github地址: <a
                href="https://github.com/lewis878/contacts-like">https://github.com/lewis878/contacts-like</a>
              </p>
              <p>预览地址: <a href="https://cjycc.github.io/contacts-like/index.html">https://cjycc.github.io/contacts-like/index.html</a></p>
            </div>
          </li>
          <li>更多内容请访问我的github</li>
        </ul>
      </div>
    </transition>
  </div>
</template>

<script>
  export default {
    name: "projects",
    data() {
      return {
        show: false
      }
    },
    mounted() {
      clearTimeout(this.timer)
      setTimeout(() => {
        this.show = true
      }, 800)
    }
  }
</script>

<style lang="scss">
  .projects {
    padding: 10px 0;
    /*text-align: center;*/
    > h1 {
      padding: 10px 0;
      font-size: 18px;
      font-weight: bold;
      text-align: center;
      margin-bottom: 10px;
    }
    .main {
      margin: 0 5%;
      @media screen and (min-width: 769px) {
        margin: 10px 15% 0;
      }
      ul {
        display: flex;
        flex-wrap: wrap;
        li {
          padding: 10px;
          margin-bottom: 10px;
          width: 100%;
          background: rgba(255, 255, 255, 0.7);
          border-radius: 10px;
          color: #555;
          @media screen and (min-width: 769px) {
            width: 48%;
            margin-right: 10px;
          }
          .title {
            font-size: 16px;
            font-weight: 700;
            text-align: center;
          }
          a {
            color: #0366d6;
          }
        }
      }
    }
  }
</style>



