<template>
  <div class="info">
    <h1>基本资料</h1>
    <transition enter-active-class="animated rollIn fast">
      <div class="main" v-show="show">
        <img src="../common/img/avatar.jpg" width="100" height="100" class="avatar">
        <h2 class="quote">风起于青萍之末,浪成于微澜之间</h2>
        <p>我叫曹锦玉</p>
        <p>Web前端开发工程师</p>
        <div class="state">
          <ul>
            <li>
              <div class="box animated bounce" style="animation-delay: 1s">
                <i class="icon-gender"></i>
              </div>
              <span>性别/男</span>
            </li>
            <li>
              <div class="box animated bounce" style="animation-delay: 1.5s">
                <i class="icon-birth"></i>
              </div>
              <span>出生/1995-02-09</span>
            </li>
            <li>
              <div class="box animated bounce" style="animation-delay: 2s">
                <i class="icon-school"></i>
              </div>
              <span>学校/郑州大学</span>
            </li>
            <li>
              <div class="box animated bounce" style="animation-delay: 2.5s">
                <i class="icon-zuobiao"></i>
              </div>
              <span>坐标/江苏泰州</span>
            </li>
          </ul>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
  export default {
    name: "info",
    data() {
      return {
        show: false
      }
    },
    mounted() {
      clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        this.show = true
      }, 500)
    }
  }
</script>

<style lang="scss">
  .info {
    padding: 10px 0;
    text-align: center;
    > h1 {
      padding: 10px 0;
      font-size: 18px;
      font-weight: bold;
    }
    .main {
      margin: 10px 5% 0;
      padding-top: 10px;
      background: rgba(0, 0, 0, 0.4);
      border-radius: 10px;
      transition: all 0.3s ease;
      @media screen and (min-width: 768px) {
        margin: 10px 20% 0;
      }
      @media screen and (min-width: 992px) {
        margin: 10px 24% 0;
      }
      &:hover {
        box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2);
      }
      .avatar {
        border-radius: 50%;
      }
      .quote {
        margin-top: 10px;
        font-size: 16px;
        margin-bottom: 10px;
      }
      p {
        line-height: 24px;
      }

      .state {
        margin-top: 10px;
        ul {
          display: flex;
          align-items: center;
          flex-wrap: wrap;
          li {
            width: 50%;
            @media screen and (min-width: 769px) {
              width: 25%;
            }
            margin-bottom: 10px;
            transition: all 0.3s ease;
            .box {
              width: 50px;
              height: 50px;
              text-align: center;
              line-height: 50px;
              margin: 0 auto;
              i {
                font-size: 30px;
              }
            }
          }
        }
      }
    }
  }
</style>



