<template>
  <div id="app" v-cloak>
    <div class="bg" :style="{backgroundImage:`url(${bgImg})`}"></div>
    <transition enter-active-class="animated zoomInDown" leave-active-class="animated zoomOutUp">
      <div class="section" v-if="currentIndex === 0">
        <info></info>
      </div>
    </transition>

    <transition enter-active-class="animated zoomInDown" leave-active-class="animated zoomOutUp">
      <div class="section" v-if="currentIndex === 1">
        <skills></skills>
      </div>
    </transition>

    <transition enter-active-class="animated zoomInDown" leave-active-class="animated zoomOutUp">
      <div class="section" v-if="currentIndex === 2">
        <projects></projects>
      </div>
    </transition>

    <transition enter-active-class="animated zoomInDown" leave-active-class="animated zoomOutUp">
      <div class="section" v-if="currentIndex === 3">
        <contact></contact>
      </div>
    </transition>

    <div id="btn">
      <i @click="next" class="icon-next"></i>
    </div>
  </div>
</template>

<script>

  import info from './components/info'
  import skills from './components/skills'
  import projects from './components/projects'
  import contact from './components/contact'


  export default {
    name: 'App',
    components: {info, skills, projects, contact},
    data() {
      return {
        bgImg: require('./common/img/avatar.jpg'),
        currentIndex: 0,
        length: 4,
        lastScroll: 0
      }
    },
    mounted() {
      // pc端
      window.onwheel = (e) => {
        var diff = e.timeStamp - this.lastScroll
        if (diff > 1000) { //滚动时间间隔大于1秒
          // 将这一次的滚动时间记录为上一次的滚动时间
          this.lastScroll = e.timeStamp
          if (e.deltaY > 0) {
            //down
            this.currentIndex++
          } else {
            this.currentIndex--
          }
        }

        //边界处理
        if (this.currentIndex >= this.length) {
          this.currentIndex = 0
        } else if (this.currentIndex < 0) {
          this.currentIndex = this.length - 1
        }
      }
    },
    methods: {
      next() {
        this.currentIndex++
        if (this.currentIndex >= this.length) {
          this.currentIndex = 0
        }
      }
    }
  }
</script>

<style lang="scss">
  @import "./common/scss/var";

  #app {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    .bg {
      width: 100%;
      height: 100%;
      background-position: 50%;
      background-repeat: no-repeat;
      background-size: cover;
      filter: blur(20px);
      opacity: 0.6;
    }

    .section {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      overflow-y: auto;
      overflow-x: hidden;
      -webkit-overflow-scrolling: touch;
      background: rgba(0, 0, 0, 0.4);
    }

    #btn {
      width: 100%;
      height: 100px;
      position: fixed;
      bottom: 20px;
      text-align: center;
      line-height: 100px;
      animation: move 5s infinite;
      i {
        cursor: pointer;
        font-size: 60px;
        color: $color-text;
      }
    }
  }

  @keyframes move {
    0%, 20%, 50%, 80%, 100% {
      transform: translateY(0);
    }
    40% {
      transform: translateY(-30px);
    }
    60% {
      transform: translateY(-15px);
    }
  }
</style>



